import { getProjectByIdStatic, getSimilarProjectsStatic, PROJECT_IDS } from '@/utils/projectsDataGenerator'
import React from 'react'
import ProjectDetailsClient from './ProjectDetailsClient'
import { Metadata } from 'next'

interface ProjectDetailsProps {
  params: { id: string }
}


// Generate metadata for SEO
export async function generateMetadata({ params }: ProjectDetailsProps): Promise<Metadata> {
  const { id } = await params
  const project = getProjectByIdStatic(id, mockT)
  
  if (!project) {
    return {
      title: 'Proyecto no encontrado | Friendsoft',
      description: 'El proyecto solicitado no fue encontrado.'
    }
  }

  return {
    title: `${project.title} | Portfolio de Proyectos | Friendsoft`,
    description: project.shortDescription,
    keywords: `${project.category}, ${project.client}, ${project.services.join(', ')}, proyecto, caso de éxito`,
    openGraph: {
      type: 'article',
      title: `${project.title} | Portfolio de Proyectos | Friendsoft`,
      description: project.shortDescription,
      images: [project.image],
      url: `/projects/${id}`
    }
  }
}

// Generate static params for static generation
export async function generateStaticParams() {
  return PROJECT_IDS.map((id) => ({
    id: id,
  }))
}

// Simple translation function for static generation
const mockT = (key: string, fallback?: string) => fallback || key

const ProjectDetails = async ({ params }: ProjectDetailsProps) => {
  const { id } = await params
  
  // Get project data
  const project = getProjectByIdStatic(id, mockT)
  const similarProjects = getSimilarProjectsStatic(id, mockT)

  const translations = {
    home: 'Inicio',
    projects: 'Proyectos',
    description: 'Descripción del Proyecto',
    results: 'Resultados del Proyecto',
    category: 'Categoría',
    client: 'Cliente',
    services: 'Servicios',
    relatedProject: 'Proyecto Relacionado',
    similarProjects: 'Proyectos Similares',
    backToProjects: 'Volver a Proyectos'
  }

  if (!project) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <ProjectDetailsClient
        project={project}
        similarProjects={similarProjects}
        translations={translations}
      />
    </div>
  )
}

